import React from "react";
function SideBar(props) {
    const {data} = props;
    return (
        <div className="container sticky-top mt-3">
            <div className="card">
                <div className="card-header bg-light">
                    <h4 className="text-center">MY TEAM</h4>
                </div>
                <div className="card-body">
                    <div className="d-flex justify-content-around">
                        <h5>Total Player: {data.length}</h5>
                        <h5>Total Cost: {
                            data.reduce((acc, cur)=> acc+cur.salary,0)+"$"
                        }</h5>
                    </div>
                    <br/>
                    <ul className="list-group">
                        {data.map(player=>(
                            <li key={Math.random()} className="list-group-item">
                                <div className="d-flex flex-row align-items-center justify-content-between">
                                    <div className="d-flex align-items-center">
                                        <img src={player.profile_picture}
                                             className="rounded-circle border border-info mini-thumbnail mr-3"
                                             alt={player.name}/>
                                        <h4>{player.name}</h4>
                                    </div>
                                    <h5 className="m-1 p-2 badge-pill badge-info">${player.salary}</h5>
                                </div>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
        </div>
    )
}
export default SideBar;