import React from "react";
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {useEffect, useState} from "react";
import players from "./data/players";
import Player from "./components/player";
import SideBar from "./components/sidebar";
import Header from "./components/header";

function App() {
    const [data, setData] = useState([]);
    const [selectedPlayers, setSelectedPlayers] = useState([]);
    useEffect(()=>{
        setData(players);
    },[]);
    const addPlayer = data => {
        if (!selectedPlayers.find(player=>player.id===data.id)){
            setSelectedPlayers([...selectedPlayers,data]);
        } else {
            setSelectedPlayers(selectedPlayers.filter(player=>data.id !== player.id))
        }
    }
  return (
    <div>
        <Header/>
        <div className="main">
            <div className="players">
                <div className="container-fluid d-flex flex-wrap">
                    {data.map(player=>(
                        <Player
                            key={Math.random()}
                            data={player}
                            handleClick={addPlayer}
                            available={selectedPlayers}
                        />
                    ))}
                </div>
            </div>
            <div className="sidebar position-sticky">
                <SideBar data={selectedPlayers}/>
            </div>
        </div>
    </div>
  );
}

export default App;
