import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faUserMinus, faUserPlus} from "@fortawesome/free-solid-svg-icons";

function Player(props) {
    const {data, handleClick} = props;
    const {id,name,age,batting_style,bowling_style,category,profile_picture,salary} = data;
    return (
        <div className="card m-2" style={{width: '18rem'}}>
            <img
                style={{width:'auto',height:'250px'}}
                src={profile_picture} className="img-thumbnail"
                alt={name}
            />
            <div className="card-body d-flex flex-column justify-content-between">
                <div>
                    <h4 className="card-title">{name}</h4>
                    <p className="card-text m-0"><strong>Age :</strong> {age}</p>
                    <p className="card-text m-0"><strong>Category :</strong> {category}</p>
                    <p className="card-text m-0"><strong>Batting Style:</strong> {batting_style}</p>
                    <p className="card-text m-0"><strong>Bowling Style :</strong> {bowling_style}</p>
                    <p className="card-text m-0"><strong>Salary :</strong> {salary}$</p>
                </div>
                <div className="mt-3">
                    <button className="btn btn-primary"
                        onClick={()=>handleClick(data)}>
                        {
                            !props.available.find(item=>item.id === id) ?
                            <span><FontAwesomeIcon icon={faUserPlus}/> Add To Team</span> :
                            <span><FontAwesomeIcon icon={faUserMinus}/> Remove From Team</span>
                        }
                    </button>
                </div>
            </div>
        </div>
    )
}
export default Player;